.main{
  --column_count: 1;
  display: grid;
  grid-template-columns: repeat(var(--column_count, 10), 22.5rem);
  grid-gap: 5rem;
  margin: 7.2rem auto 0 auto;
  width: max-content;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .main{
    --column_count: 2;
  }
}
@media (min-width: 1024px) {
  .main {
    --column_count: 3;
  }
}